import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const QuickFormulaCaseStudy = () => (
  <Layout>
    <SEO title="Quick Formula" />

  <div class="w-container">
  <div class="header">Quick Formula</div>
  <div class="header-dates">Oct 2017 - Jan 2018</div>
  <div class="description">Using activity and sleep data, mobile graphs show the user an organized view of his or her health. When the user is reminded of how he or she did yesterday, he or she is  more motivated to do even better today.</div>
  <div class="case-spacer-line"></div>
</div>

<div class="case-body" data-ix="show-navbar">
   <div class="container-5 w-container">
      <div class="case-text-role">Role: Interaction Design, Visual Design</div>
      <div class="case-text">In 2016, Pebble decided to turn its focus to health and fitness. The members of the Product Design team racked their brains trying to come up with a vision for a “health tracker.” The market is saturated with health devices that are similar in their approaches; we wanted to come up with something that felt more natural and was less goal-oriented than the other devices.</div>
      <div class="case-spacer-24"></div>
      <div class="case-text">I designed the mobile experience of Pebble Health for iOS and Android apps, including the presentation of activity, sleep sessions, heart rate information and workouts, which were backed up by graphs where the user would be measured against his or her average.</div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d88108370c380a953_dc76c468a69f3b8f78d611ee_rw_1200.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d88108370c380a953_dc76c468a69f3b8f78d611ee_rw_1200-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d88108370c380a953_dc76c468a69f3b8f78d611ee_rw_1200-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d88108370c380a953_dc76c468a69f3b8f78d611ee_rw_1200.png 1200w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 940px" alt="" class="case-image"/>
      <div class="case-text-tiny">Pebble Health cards in iOS app</div>
      <div class="case-spacer-64"></div>
      <div class="header-2">Interaction Design</div>
      <div class="case-text">One of the main goals was scalability. At the moment of launch, we had only two sources of data: user activity and sleep. The design solution should be flexible enough to easily add more sources of data: heart rate, workouts, stairs taken and more. I conceived of  a cards-based interface. By using them, we were able to add more and more data sources in a very easy way, virtually without any limits.</div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810833a2080a954_803255b898bb6b636137a31b_rw_1200.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810833a2080a954_803255b898bb6b636137a31b_rw_1200-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810833a2080a954_803255b898bb6b636137a31b_rw_1200-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810833a2080a954_803255b898bb6b636137a31b_rw_1200-p-1080.png 1080w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810833a2080a954_803255b898bb6b636137a31b_rw_1200.png 1200w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 940px" alt="" class="case-image"/>
      <div class="case-text-tiny">Sketches showing the idea of using cards</div>
      <div class="case-spacer-64"></div>
      <div class="header-2">Visual Design Concepts And Exploration</div>
      <div class="case-text">It was very important to visually communicate the concept of the &quot;typical,&quot; a user’s average that constantly adjusts based on his or her performance. In addition to the numerical value of steps and sleep, I designed the graph to visually show how a user is performing against his or her changing &quot;typical.&quot; In effect, the user is  not just racing against the clock, but against himself or herself.</div>
      <div class="case-spacer-24"></div>
      <div class="case-text">Another goal was to show some useful insights based on the data collected  from a user’s device. I wanted to avoid merely presenting boring numbers on a dashboard. Hence, I designed graphs to allow users to see e progression or regression in activities, sleep patterns  and heart rate data.</div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083656080a955_6b71b8091c54a628ed234ec4_rw_1200.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083656080a955_6b71b8091c54a628ed234ec4_rw_1200-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083656080a955_6b71b8091c54a628ed234ec4_rw_1200-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083656080a955_6b71b8091c54a628ed234ec4_rw_1200.png 933w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 933px" alt="" class="case-image"/>
      <div class="case-text-tiny">Few milestones of visual design explorations</div>
      <div class="case-spacer-64"></div>
      <div class="header-2">Visual Design Evolution</div>
      <div class="case-text">I spent a lot of time trying to apply Pebble&#x27;s visual language that we were using on the watch to mobile apps. This goal was truly challenging. Merging the unique visual style and mobile platform guidelines took some time to get right.</div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083bb1280a956_c66b62e18259c7858a1111a4_rw_1920.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083bb1280a956_c66b62e18259c7858a1111a4_rw_1920-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083bb1280a956_c66b62e18259c7858a1111a4_rw_1920-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083bb1280a956_c66b62e18259c7858a1111a4_rw_1920-p-1080.png 1080w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083bb1280a956_c66b62e18259c7858a1111a4_rw_1920-p-1600.png 1600w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083bb1280a956_c66b62e18259c7858a1111a4_rw_1920.png 1920w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 940px" alt="" class="case-image"/>
      <div class="case-text-tiny">Activity card design evolution over time</div>
      <div class="case-spacer-24"></div>
      <div class="case-spacer-64"></div>
      <div class="header-2">Final Visual Designs</div>
      <div class="case-text">After over 50 iterations during about 18 months of design work, over 140 screens and layouts were designed for the final delivery.</div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083347480a957_bf553884972a5f55344868fb_rw_1920.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083347480a957_bf553884972a5f55344868fb_rw_1920-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083347480a957_bf553884972a5f55344868fb_rw_1920-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083347480a957_bf553884972a5f55344868fb_rw_1920-p-1080.png 1080w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083347480a957_bf553884972a5f55344868fb_rw_1920.png 1848w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 940px" alt="" class="case-image"/>
      <div class="case-text-tiny">Activity Graphs</div>
      <div class="case-spacer-24"></div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083205480a959_e8f557746e47e97799bf0c1b_rw_1920.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083205480a959_e8f557746e47e97799bf0c1b_rw_1920-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083205480a959_e8f557746e47e97799bf0c1b_rw_1920-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083205480a959_e8f557746e47e97799bf0c1b_rw_1920-p-1080.png 1080w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083205480a959_e8f557746e47e97799bf0c1b_rw_1920.png 1848w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 940px" alt="" class="case-image"/>
      <div class="case-text-tiny">Sleep Graphs</div>
      <div class="case-spacer-24"></div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083458580a95a_b87e56a7fce8d816d77ac037_rw_1920.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083458580a95a_b87e56a7fce8d816d77ac037_rw_1920-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083458580a95a_b87e56a7fce8d816d77ac037_rw_1920-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083458580a95a_b87e56a7fce8d816d77ac037_rw_1920-p-1080.png 1080w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083458580a95a_b87e56a7fce8d816d77ac037_rw_1920.png 1848w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 940px" alt="" class="case-image"/>
      <div class="case-text-tiny">Heart Rate Graphs</div>
      <div class="case-spacer-24"></div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083dccc80a958_9276c41f6581d1cdff554803_rw_1920.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083dccc80a958_9276c41f6581d1cdff554803_rw_1920-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083dccc80a958_9276c41f6581d1cdff554803_rw_1920-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083dccc80a958_9276c41f6581d1cdff554803_rw_1920-p-1080.png 1080w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083dccc80a958_9276c41f6581d1cdff554803_rw_1920-p-1600.png 1600w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d881083dccc80a958_9276c41f6581d1cdff554803_rw_1920.png 1920w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 940px" alt="" class="case-image"/>
      <div class="case-text-tiny">Workouts Cards</div>
      <div class="case-spacer-64"></div>
      <div class="header-2">Edge Cases and Additional States</div>
      <div class="case-text">When working on a project involving any type of data visualization, it&#x27;s really important to get your designs to work with a real data. I&#x27;ve seen hundreds of attractive styles on Dribbble that will not work in real life. I had to do my best to make this happen for designs crafted for Pebble Health.</div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d88108339ac80a95b_408976774e61c3b27a834ece_rw_1200.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d88108339ac80a95b_408976774e61c3b27a834ece_rw_1200-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d88108339ac80a95b_408976774e61c3b27a834ece_rw_1200-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d88108339ac80a95b_408976774e61c3b27a834ece_rw_1200.png 1200w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 940px" alt="" class="case-image"/>
      <div class="case-text-tiny">Some of the edge cases covered</div>
      <div class="case-spacer-24"></div>
      <img src="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810830a6680a95c_a58862a324bcef59d0e783b1_rw_1920.png" srcset="https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810830a6680a95c_a58862a324bcef59d0e783b1_rw_1920-p-500.png 500w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810830a6680a95c_a58862a324bcef59d0e783b1_rw_1920-p-800.png 800w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810830a6680a95c_a58862a324bcef59d0e783b1_rw_1920-p-1080.png 1080w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810830a6680a95c_a58862a324bcef59d0e783b1_rw_1920-p-1600.png 1600w, https://uploads-ssl.webflow.com/5b10d84d88108327e480a8a5/5b10d84d8810830a6680a95c_a58862a324bcef59d0e783b1_rw_1920.png 1920w" sizes="(max-width: 479px) 93vw, (max-width: 991px) 73vw, 940px" alt="" class="case-image"/>
      <div class="case-text-tiny">User education cards</div>
      <div class="case-spacer-64"></div>
      <div class="case-text center">Available for <a href="https://itunes.apple.com/us/app/pebble/id957997620?mt=8" target="_blank" class="case-link">iPhone</a> and <a href="https://play.google.com/store/apps/details?id=com.getpebble.android.basalt&amp;hl=en" target="_blank" class="case-link">Android</a>. Pebble Time or Pebble 2 watch series required.</div>
      <div class="case-spacer-64"></div>
      <Link className="rewind" to="/">Back to Homepage</Link>
   </div>
</div>
  </Layout>
)

export default QuickFormulaCaseStudy